@import 'var.scss';

// background image anim

@-webkit-keyframes bg {
  0% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0;
  }

  25% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0.15;
  }

  50% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0;
  }

  51% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0;
  }

  75% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0.15;
  }

  100% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0;
  }
}

@keyframes bg {
  0% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0;
  }

  25% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0.15;
  }

  50% {
    background: url('/assets/img/bgt1.jpg');
    opacity: 0;
  }

  51% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0;
  }

  75% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0.15;
  }

  100% {
    background: url('/assets/img/bgt2.jpg');
    opacity: 0;
  }
}

@-webkit-keyframes intro {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes intro {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes post_intro {
  0% {
    opacity: 0;
    pointer-events: none;
  }

  100% {
    opacity: 0.75;
    pointer-events: all;
  }
}

@keyframes post_intro {
  0% {
    opacity: 0;
    pointer-events: none;
  }

  100% {
    opacity: 0.75;
    pointer-events: all;
  }
}

@-webkit-keyframes typing {
  from {
    width: 0
  }

  to {
    width: 100%
  }
}

@keyframes typing {
  from {
    width: 0
  }

  to {
    width: 100%
  }
}




#bganim {
  width: 100%;
  height: 100%;
  background-size: cover;
  position: absolute;
  z-index: -100;
  -webkit-animation: infinite 20s bg;
  animation: infinite 20s bg;
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}


//main homepage layout

#homepage {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100cqh;
  width: 100%;
}

#fxcontain {
  max-width: 1536px !important;
  max-height: 882px !important;
  width: 80%;
  //height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  -moz-column-gap: 2vw;
  column-gap: 2vw;
  padding: 3vh 5vw 4vh 4vw;

  @media (max-width: 1400px) {
    width: 100%;
  }

  @media (max-width: 1200px) {
    padding: 5vh 5vw 0 4vw;
  }

}

//#fxcontain div{ background: white; }

#fxside {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 14vw;
  max-width: 14vw;
  box-sizing: border-box;
}

#avatar {
